<template>
	<div>
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/list' }">列表</el-breadcrumb-item>
			<el-breadcrumb-item>详情</el-breadcrumb-item>
		</el-breadcrumb>
		<div>
			<el-col :span="16">
				<ul class="detail-list">
					<li><span>会议ID：</span>{{data.meetingId}}</li>
					<li><span>会议主题：</span>{{data.meetingTopic}}</li>
					<li><span>开会时间：</span>{{data.meetingBeginDtStr}}</li>
					<li><span>结会时间：</span>{{data.meetingEndDtStr}}</li>
					<li><span>会议状态：</span>{{data.meetingStatusCode}}</li>
					<li><span>会议地址：</span>{{data.localGps}}</li>					
					<li class="content"><span>会议内容：</span>{{data.content}}</li>
				</ul>
			</el-col>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
			data:''
		}
	},
	components: {

	},
	mounted(){
		this.$http({
			url: this.requestUrl+'/meeting/ptMeetingInfo/ptGetMeetingInfo',
			headers:{
				'Access-Control-Allow-origin':'*',
			},
			method: 'post',
			responseType: 'json', // 默认的
			params: {
				meetingId:this.$route.params.meetingId
			}
		}).then(response => {
			console.log(response.data);
			this.data=response.data.data;
		}).catch(response => {
			this.$message.error(response.data.message);
		});
	}
}
</script>

<style scoped>
.detail-list{margin-top:20px;}
.detail-list li{
	position: relative;
	line-height:40px;
	min-height:40px;
	padding-left:80px;
	border-bottom:1px dashed #ddd;
}
.detail-list li.content{
	line-height:28px;
}
.detail-list span{
	position: absolute;
	text-align:right;
	width:80px;
	left:0;
	top:0;
	bottom:0;
}
</style>



