<template>
	<div>
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/list' }">列表</el-breadcrumb-item>
			<el-breadcrumb-item>编辑</el-breadcrumb-item>
		</el-breadcrumb>
		<div class="form-table">
			<el-form :label-position="'right'" label-width="80px" :model="formData" size="small">
				<el-col :span="10">
					<el-form-item label="会议主题">
						<el-input v-model="formData.meetingTopic"></el-input>
					</el-form-item>
					<el-form-item label="会议内容">
						<el-input v-model="formData.content" type="textarea" :autosize="{minRows:3}" resize="none"></el-input>
					</el-form-item>
					<el-form-item label="会议状态">
						<el-select class="width100" v-model="formData.meetingStatusCode" placeholder="请选择" >
							<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.name"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="开会时间">
						<el-date-picker class="width100" v-model="date" value-format="yyyy-MM-dd HH:mm:ss" type="datetimerange" start-placeholder="开会时间" end-placeholder="结会时间" :picker-options="pickTime"></el-date-picker>
					</el-form-item>
					<el-form-item label="会议地址">
						<el-input v-model="formData.localGps"></el-input>
					</el-form-item>
					<el-button type="primary" class="ml80" size="medium" @click="submit">保存</el-button>
				</el-col>
			</el-form>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
			data:'',
			formData:{
				meetingTopic:'',
				meetingBeginDt:'',
				meetingEndDt:'',
				content:'',
				meetingStatusCode:'',
				localGps:'',
				meetingId:this.$route.params.meetingId,
				appId:this.$route.params.appId,
				appOrgId:this.$route.params.appOrgId,
				version:this.$route.params.version
			},
			date:['',''],
			options: [
				{value:'',label:'全部',name:''},
				{value: '1',label: '编辑中',name:'editing'},
				{value: '2',label: '审批中',name:'approving'},
				{value: '3',label: '可用',name:'available'},
				{value: '4',label: '停用',name:'disable'},
				{value: '5',label: '驳回',name:'reject'}
			],
			pickTime: {
				disabledDate(time) {
					return time.getTime() < Date.now()
				}
			}
		}
	},
	components: {

	},
	mounted(){
		this.$http({
			url: this.requestUrl+'/meeting/ptMeetingInfo/ptGetMeetingInfo',
			headers:{
				'Access-Control-Allow-origin':'*',
			},
			method: 'post',
			responseType: 'json', // 默认的
			params: {
				meetingId:this.$route.params.meetingId
			}
		}).then(response => {
			let data=response.data.data;
			this.data=data;
			this.formData.meetingTopic=data.meetingTopic;
			this.formData.content=data.content;
			this.formData.meetingStatusCode=data.meetingStatusCode;
			let date1=data.meetingBeginDt;
			let date2=data.meetingEndDt;
			this.date=[new Date(date1.year+1900,date1.month,date1.date,date1.hours,date1.minutes,date1.seconds),new Date(date2.year+1900,date2.month,date2.date,date2.hours,date2.minutes,date1.seconds)];
			this.formData.localGps=data.localGps;
		}).catch(response => {
			this.$message.error(response.data.message);
		});
	},
	methods:{
		submit(){
			if(!this.formData.meetingTopic){
				this.$message.error('请输入会议主题');
			}else if(!this.formData.content){
				this.$message.error('请输入会议内容');
			}else if(!this.date){
				this.$message.error('请选择开会时间');
			}else if(!this.formData.localGps){
				this.$message.error('请选择会议地址');
			}else if(!this.formData.meetingStatusCode){
				this.$message.error('请选择会议状态');
			}else{
				this.formData.meetingBeginDt=this.date[0];
				this.formData.meetingEndDt=this.date[1];
				if(new Date(this.date[0]).getTime()==this.data.meetingBeginDt.time&&new Date(this.date[1]).getTime()!==this.data.meetingEndDt.time){
					this.formData.meetingBeginDt=this.data.meetingBeginDtStr+':00';
				}else if(new Date(this.date[1]).getTime()==this.data.meetingEndDt.time&&new Date(this.date[0]).getTime()!==this.data.meetingBeginDt.time){
					this.formData.meetingEndDt=this.data.meetingEndDtStr+':00';
				}else if(new Date(this.date[0]).getTime()==this.data.meetingBeginDt.time&&new Date(this.date[1]).getTime()==this.data.meetingEndDt.time){
					this.formData.meetingBeginDt=this.data.meetingBeginDtStr+':00';
					this.formData.meetingEndDt=this.data.meetingEndDtStr+':00';
				}
				
				this.$http({
					url: this.requestUrl+'/meeting/ptMeetingInfo/ptUpdateMeetingInfo',
					headers:{
						'Access-Control-Allow-origin':'*',
					},
					method: 'post',
					responseType: 'json', // 默认的
					params: this.formData
				}).then(response => {
					console.log(response.data);
					this.$router.push({name:'list'});
				}).catch(response => {
					this.$message.error(response.data.message);
				});
			}
		}
	}
}
</script>

<style scoped>
.ml80{margin-left:80px;}
.width100{width:100%;}
.form-table{margin-top:20px;}
</style>



