<template>
  <div>
    <el-form ref="form" :model="form" :inline="true" size="small">
      <el-form-item label="会议主题：">
        <el-input v-model="form.meetingTopic" class="width180"></el-input>
      </el-form-item>
      <el-form-item label="会议状态：">
        <el-select v-model="form.value" placeholder="请选择" >
          <el-option v-for="item in form.options" :key="item.value" :label="item.label" :value="item.name"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="开会时间：">
        <el-date-picker v-model="form.meetingDate" type="date" placeholder="选择日期" value-format="yyyy-MM-dd"></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <div class="fr">
      <el-button type="primary" icon="el-icon-plus" size="medium" @click="toAdd">添加</el-button>
      <el-button type="danger" icon="el-icon-delete" size="medium" @click="toDel">删除</el-button>
    </div>
    <div class="table">
      <el-table :data="data.viewList" tooltip-effect="dark" style="width: 100%"  border @selection-change="handleSelectionChange" v-loading="data.viewList?false:true" element-loading-text="加载中" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)" @sort-change="sortChange()">
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
        <el-table-column prop="meetingTopic" label="会议主题"></el-table-column>
        <el-table-column prop="meetingBeginDt.time" label="开会时间" sortable="custom" >
          <template slot-scope="scope">
            <span v-if="scope.row.meetingBeginDt&&scope.row.meetingBeginDt.time">{{scope.row.meetingBeginDt.time | date}}</span>
          </template>
        </el-table-column>
        <el-table-column label="会议内容" show-overflow-tooltip>
          <template slot-scope="scope">
            <span>{{scope.row.contentStr?scope.row.contentStr:'暂无数据'}}</span>
          </template>
        </el-table-column>
        <el-table-column label="会议状态">
          <template slot-scope="scope">
            <span>{{scope.row.meetingStatusCode | status}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200">
          <template slot-scope="scope">
            <el-button size="small" type="primary" icon="el-icon-edit-outline" @click="toEdit(scope.row.meetingId,scope.row.appId
            ,scope.row.appOrgId,scope.row.version)">编辑</el-button>
            <el-button size="small" type="primary" icon="el-icon-more" @click="toDetail(scope.row.meetingId)">详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="paging">
      <el-pagination background :page-sizes="[10,20,30]" :page-size="10" :total="data.count" layout="total, sizes, prev, pager, next, jumper" @size-change="handleSizeChange" @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form:{
        meetingTopic:"",
        meetingDate:'',
        value:'',
        sortJudge:false,
        options: [
          {value:'',label:'全部',name:''},
          {value: '1',label: '编辑中',name:'editing'},
          {value: '2',label: '审批中',name:'approving'},
          {value: '3',label: '可用',name:'available'},
          {value: '4',label: '停用',name:'disable'},
          {value: '5',label: '驳回',name:'reject'}
        ]
      },
      data: '',
      multipleSelection: [],
      params:{
        pageNo:'',
        pageSize:'',
        meetingTopic:'',
        meetingBeginDtStr:'',
        meetingStatusCode:''
      }
    }
  },
  mounted(){
    this.getList();
  },
  methods:{
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log(this.multipleSelection)
    },
    handleSizeChange: function (size) {
      this.params.pageSize=size;
      this.getList();
    },
    handleCurrentChange: function(currentPage){
      this.params.pageNo=currentPage;
      this.getList();
    },
    // 表格按时间排序
    sortChange(){
      if(this.sortJudge==false){
        this.params.orderType='asc';
      }else{
        this.params.orderType='desc';
      }
      this.params.sortOrder='meetingBeginDt';
      this.sortJudge=!this.sortJudge;
      this.getList();
    },
    getList(){
      this.$http({
        // url: 'http://192.168.13.238:8083/iHF/meetingInfo/ptmeetinginfo/ptviewList.yh',
        url: this.requestUrl+'/meeting/ptMeetingInfo/ptListMeetingInfo',
        headers:{
          'Access-Control-Allow-origin':'*'
          // 'Content-Type': 'application/x-www-form-urlencoded'
        },
        method: 'post',
        responseType: 'json', // 默认的
        params: this.params
        // params: {
        //   ticket:'61ce2301-a3ef-4561-9838-91d8ab2e209b',
        //   service:'http://www.baidu.com'
        // }
      }).then(response => {
        if(response.data.resultMap.data){
          console.log(response.data)
          this.data = response.data.resultMap.data;
        }
      }).catch(response => {
        if(response.data){
          this.$message.error(response.data.message);
        }
      });
    },
    toAdd(){
      this.$router.push({path:'add'})
    },
    search(){
      this.params={};
      this.params.meetingTopic=this.form.meetingTopic;
      this.params.meetingBeginDtStr=this.form.meetingDate;
      this.params.meetingStatusCode=this.form.value;
      this.getList();
    },
    toDel(){
      if(this.multipleSelection.length==0){
        this.$message.error('请选择要删除的行');
      }else{
        let temp='';
        for(let i of this.multipleSelection){
          temp+=i.meetingId+',';
        }
        temp=temp.substring(0,temp.length-1);
        this.$confirm('该操作将会删除文件，是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.requestUrl+'/meeting/ptMeetingInfo/ptDeleteMeetingInfo',
            headers:{
              'Access-Control-Allow-origin':'*',
            },
            method: 'post',
            responseType: 'json', // 默认的
            params: {
              ids:temp
            }
          }).then(response => {
            this.$message.success(response.data.message?response.data.message:'删除成功');
            this.getList();
          }).catch(response => {
            this.$message.error(response.data.message);
          });
        })
      }
    },
    toEdit(id,id2,id3,version){
      this.$router.push({name:'edit',params:{meetingId:id,appId:id2,appOrgId:id3,version:version}});
    },
    toDetail(id){
      this.$router.push({name:'detail',params:{meetingId:id}});
    }
  },
  filters:{
    date(msg){
      var date=new Date(msg);
      function toDouble(obj){
        if(obj<10){
          return '0'+obj;
        }else{
          return obj;
        }
      }
      return date.getFullYear()+'-'+toDouble(date.getMonth()+1)+'-'+toDouble(date.getDate())+' '+toDouble(date.getHours())+':'+toDouble(date.getMinutes());
    },
    status(msg){
      switch(msg)
      {    
      case 'approving':
        return '审批中'
        break;
      case 'available':
        return '可用'
        break;
      case 'disable':
        return '停用'
        break;
      case 'editing':
        return '编辑中'
        break;
      case 'reject':
        return '驳回'
        break;
      default:
        return ''
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.width180{width:180px;}
.fr{margin:15px 0;}
.paging{
  margin:15px 0;
  text-align:center;
}
</style>



