<template>
	<div>
		<el-form :label-position="'right'" label-width="80px" :model="formData" size="small">
			<el-col :span="10">
				<el-form-item label="会议主题">
					<el-input v-model="formData.meetingTopic"></el-input>
				</el-form-item>
				<el-form-item label="会议内容">
					<el-input v-model="formData.content" type="textarea" :autosize="{minRows:3}" resize="none"></el-input>
				</el-form-item>
				<el-form-item label="会议状态">
					<el-select class="width100" v-model="formData.meetingStatusCode" placeholder="请选择" >
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="开会时间">
					<el-date-picker class="width100" v-model="date" value-format="yyyy-MM-dd HH:mm:ss" type="datetimerange" start-placeholder="开会时间" end-placeholder="结会时间" :picker-options="pickTime"></el-date-picker>
				</el-form-item>
				<el-form-item label="会议地址">
					<el-input v-model="formData.localGps"></el-input>
				</el-form-item>
				<el-button type="primary" class="ml80" size="medium" @click="submit">发布</el-button>
			</el-col>
		</el-form>
	</div>
</template>

<script>
export default{
	data(){
		return{
			formData:{
				meetingTopic:'',
				meetingBeginDt:'',
				meetingEndDt:'',
				content:'',
				meetingStatusCode:'',
				localGps:'广州科学城总部经济区'
			},
			date:'',
			options: [
				{value:'',label:'全部',name:''},
				{value: '1',label: '编辑中',name:'editing'},
				{value: '2',label: '审批中',name:'approving'},
				{value: '3',label: '可用',name:'available'},
				{value: '4',label: '停用',name:'disable'},
				{value: '5',label: '驳回',name:'reject'}
			],
			pickTime: {
				disabledDate(time) {
					return time.getTime() < Date.now()
				}
			}
		}
	},
	methods:{
		submit(){
			if(!this.formData.meetingTopic){
				this.$message.error('请输入会议主题');
			}else if(!this.formData.content){
				this.$message.error('请输入会议内容');
			}else if(!this.date){
				this.$message.error('请选择开会时间');
			}else if(!this.formData.localGps){
				this.$message.error('请选择会议地址');
			}else if(!this.formData.meetingStatusCode){
				this.$message.error('请选择会议状态');
			}else{
				this.formData.meetingBeginDt=this.date[0];
				this.formData.meetingEndDt=this.date[1];
				this.$http({
					url: this.requestUrl+'/meeting/ptMeetingInfo/ptAddMeetingInfo',
					headers:{
					'Access-Control-Allow-origin':'*',
					},
					method: 'get',
					responseType: 'json', // 默认的
					params: this.formData
				}).then(response => {
					if(response.data.data){
						this.$message.success('发布成功');
						this.$router.push({path:'/list'});
					}
				}).catch(response => {
					this.$message.error(response.data.message?response.data.message:"发布失败");
				});
			}
		}
	}
}
</script>

<style scoped>
.ml80{margin-left:80px;}
.width100{width:100%;}
</style>




